import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';

/**
 * Navigation bar located at the top of all pages.
 * Responsive: will adjust based on device screen size,
 *             and transition to "hamburger" menu on
 *             mobile devices.
 */
const NavbarReactive = () => {
  return (
    <Navbar sticky="top" collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Navbar.Brand href="/">Cafés</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link href="/disclaimer">Disclaimer</Nav.Link>
          <Nav.Link href="/about">About</Nav.Link>
          <Nav.Link rel="noopener noreferrer" target="_blank" href="https://github.com/simon-bruklich/COVID-19-Schools">
            GitHub
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default NavbarReactive;
